import React, {useState} from 'react';
import {S} from './../HeaderMenu_Styles'



const items = [
    {title: 'Home',
    href: 'home'},
    {title: 'About me',
        href: 'about'},
    {title: 'My potential skills',
        href: 'skills'},
    {title: 'My future works',
        href: 'works'},
    {title: 'Contacts',
        href: 'contacts'}
]

type menuPropstype = {
    onClick?: (value: boolean) => void
    menuIsOpen?: boolean
}

export const Menu: React.FC<menuPropstype> = ({onClick, menuIsOpen}) => {
    const onClickHandler = () => {
        onClick && onClick(!menuIsOpen)
    }
    return (

        <ul>
            {items.map((item, index)=>{
                return <li key={index}>
                    <S.NavLink to={item.href} smooth={true} offset={-60} activeClass={'active'}  onClick={onClickHandler}>{item.title}</S.NavLink>
                </li>
            })}

        </ul>
    );
};

